const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			navList: [
				{ id: '3', img: 'iconbulletin', title: '微金公告',type: '23'},
				{ id: '6', img: 'iconqiapiansousuo', title: '互动搜索',type: '' },
				{ id: '2', img: 'iconliebiao', title: '贷款产品互动',type: '1'},	
				{ id: '4', img: 'iconyonghu', title: '信用卡互动',type: '2' },
				{ id: '5', img: 'iconqiapiansousuo', title: '推荐热帖',type: '3' },
				{ id: '1', img: 'iconxiepinglun', title: '网友互动',type: ''},
			],
			imgUrl:'',
			isShow: false,
			categoryList:[],
			subCategoryList:[],
			categoryActive: 0,
			area_id: '',
			token: ''
		};
	},
	onLoad() {	
		this.imgUrl = helper.imgUrl
		this.token = uni.getStorageSync('token')
	},
	onShow(){	
		this.getUserInfo()
		this.categoryActive = 0,
		this.subCategoryList =[]
		// 加载微金交流首页数据
		this.getRegion();
	},
	methods: {
		// 左边导航点击事件
		categoryMainClick(e,index){
			console.log(e,index);
			this.categoryActive = index;
			this.area_id = e
			this.subCategoryList =[]
			this.getList()
		},
		// 详情
		categorySubClick(title,id,img,fatheId){
			uni.navigateTo({
				url:`/pages/exchangList?title=${title}&id=${id}&img=${img}&fatheId=${fatheId}`
			})
		},
		// 获取用户信息
		getUserInfo(){
			uni.request({
				url: `${helper.requestUrl}/me`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res,'++++++++')
					let user = res.data.type
					let vipType = res.data.vip_id
					if(user == 'normal'){
						this.isShow = true
					}else{
						if(vipType == '2' || vipType == '3'){
							this.isShow = false
						}else{
							this.isShow = true
						}
					}
					// this.userInfo = res.data
				}
			})
		},
		// 数据
		getRegion() {
			uni.request({
				url: `${helper.requestUrl}/areas/area-list`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == '200') {
						this.categoryList = res.data.data;
						this.area_id = this.categoryList[0].id
						this.getList()
					} else {
						if(res.data.message == '用户不存在或登陆已过期'){
							uni.showToast({
								title: '未检测到用户的登录记录，请进行登录',
								icon: 'none',
								duration: 3000
							});
							setTimeout(() => {
								// 进入登录页
								uni.reLaunch({
									url: './login'
								});
							}, 3000)
						}else{
							uni.showToast({
								title: res.data.message,
								icon: 'none',
								duration: 3000
							});
						}
					}
				}
			});
		},
		// 导航子页面跳转
		getNav(id,title,img) {
			if(id != ''){
				uni.navigateTo({
					url: `/pages/indexA?id=${id}&name=${title}`
				});
			}else{
				if(title == '网友互动'){
					uni.navigateTo({
						url:`/pages/boardData?title=${title}&id=${38}&img=${img}`
					})
				}
				
			}
			
		},
		// 加载下一页
		getList() {
			uni.showLoading({
				title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/areas/city-list`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				data:{
					area_id: this.area_id
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == '200') {
						this.subCategoryList = res.data.data;
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		},
	}
};
